import Color from 'color';
import { ImageResponse } from 'next/og';
import { NextRequest } from 'next/server';

import { OGImage } from '@/og/OGImage';
import { fetchFonts } from '@/og/fetchFonts';

export const config = {
  runtime: 'edge',
};

const fontsPromise = fetchFonts();

export default async function handler(req: NextRequest) {
  try {
    const fonts = await fontsPromise;
    const { searchParams } = new URL(req.url);

    const division = searchParams.get('division');
    const section = searchParams.get('section');
    const title = searchParams.get('title');
    const description = searchParams.get('description');
    const primaryColor = searchParams.get('primaryColor');
    const lightColor = searchParams.get('lightColor');

    const rawMode = searchParams.get('mode')?.toLowerCase();

    const mode =
      rawMode === 'light' || rawMode === 'dark'
        ? rawMode
        : primaryColor && Color(primaryColor).isLight()
          ? 'light'
          : 'dark';

    const logo = searchParams.get(mode === 'light' ? 'logoLight' : 'logoDark');

    return new ImageResponse(
      (
        <OGImage
          primaryColor={primaryColor}
          lightColor={lightColor}
          logo={logo}
          division={division}
          section={section}
          title={title}
          description={description}
          mode={mode}
        />
      ),
      {
        headers: {
          'Cache-Control': 'public, max-age=86400, s-maxage=604800',
        },
        fonts,
      }
    );
  } catch (e) {
    console.error(e);
    return new Response(`Failed to generate the image`, {
      status: 500,
    });
  }
}
