import type { SatoriOptions } from 'satori';

export const fetchFonts = async (): Promise<SatoriOptions['fonts']> => [
  {
    name: 'Inter',
    data: await (
      await fetch(new URL('../../assets/Inter-Regular.ttf', import.meta.url))
    ).arrayBuffer(),
    style: 'normal',
    weight: 400,
  },
  {
    name: 'Inter',
    data: await (
      await fetch(new URL('../../assets/Inter-SemiBold.ttf', import.meta.url))
    ).arrayBuffer(),
    style: 'normal',
    weight: 600,
  },
  {
    name: 'Inter',
    data: await (
      await fetch(new URL('../../assets/Inter-Bold.ttf', import.meta.url))
    ).arrayBuffer(),
    style: 'normal',
    weight: 700,
  },
];
